'use client';

import React, { useState, useEffect } from 'react';
import { useRouter, useParams } from 'next/navigation';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import { Button } from '@/components/Button';
import { Input } from '@/components/Input';
import { ImageUpload } from '@/components/ImageUpload';
import { Wave } from '@/components/Wave';
import { moviesApi } from '@/lib/api/movies';
import { useAuthStore } from '@/lib/store';

const updateMovieSchema = z.object({
  title: z.string().min(1, 'Title is required'),
  publishingYear: z
    .number({
      required_error: 'Publishing year is required',
      invalid_type_error: 'Publishing year must be a number',
    })
    .int('Publishing year must be a whole number (e.g., 1999, 2000)')
    .min(1800, 'Publishing year must be between 1800 and 2035')
    .max(2035, 'Publishing year must be between 1800 and 2035'),
  poster: z.instanceof(File).optional(),
});

type UpdateMovieFormData = z.infer<typeof updateMovieSchema>;

export default function EditMoviePage() {
  const router = useRouter();
  const params = useParams();
  const movieId = params.id as string;
  const [error, setError] = useState<string>('');
  const [isLoading, setIsLoading] = useState(false);
  const [isLoadingMovie, setIsLoadingMovie] = useState(true);
  const [posterFile, setPosterFile] = useState<File | null>(null);
  const [posterUrl, setPosterUrl] = useState<string>('');

  const {
    register,
    handleSubmit,
    formState: { errors },
    setValue,
    reset,
  } = useForm<UpdateMovieFormData>({
    resolver: zodResolver(updateMovieSchema),
  });

  useEffect(() => {
    const { initialize, isAuthenticated: auth } = useAuthStore.getState();
    initialize();
    if (!auth) {
      router.push('/login');
      return;
    }
    loadMovie();
  }, [movieId, router]);

  const loadMovie = async () => {
    try {
      setIsLoadingMovie(true);
      const movie = await moviesApi.getById(movieId);
      reset({
        title: movie.title,
        publishingYear: movie.publishingYear,
      });
      setPosterUrl(movie.poster);
    } catch (err: any) {
      setError(err.response?.data?.message || 'Failed to load movie');
    } finally {
      setIsLoadingMovie(false);
    }
  };

  const onSubmit = async (data: UpdateMovieFormData) => {
    try {
      setIsLoading(true);
      setError('');
      await moviesApi.update(movieId, {
        title: data.title,
        publishingYear: data.publishingYear,
        poster: posterFile || undefined,
      });
      router.push('/movies');
    } catch (err: any) {
      // Format backend validation errors with proper spacing
      if (err.response?.data?.errors && Array.isArray(err.response.data.errors)) {
        const errorMessages = err.response.data.errors
          .map((e: any) => e.message)
          .filter((msg: string) => msg) // Remove empty messages
          .join('\n'); // Use newline for better spacing
        setError(errorMessages);
      } else {
        setError(err.response?.data?.message || 'Failed to update movie. Please check your input.');
      }
    } finally {
      setIsLoading(false);
    }
  };

  const handleCancel = () => {
    router.push('/movies');
  };

  if (isLoadingMovie) {
    return (
      <section className="form-page px-7 py-12 pb-[160px] relative">
        <div className="flex items-center justify-center min-h-[400px]">
          <p className="text-white/70">Loading...</p>
        </div>
      </section>
    );
  }

  return (
    <section className="form-page px-7 py-12 pb-[160px] relative">
      <h2 className="form-page__title text-[48px] mb-8 m-0 font-bold">
        Edit
      </h2>
      <div className="form-grid grid grid-cols-1 lg:grid-cols-[420px_1fr] gap-12 items-start">
        <ImageUpload
          value={posterFile || posterUrl}
          onChange={(file) => {
            setPosterFile(file);
            if (file) {
              setValue('poster', file, { shouldValidate: true });
            }
          }}
          error={errors.poster?.message as string}
        />
        <form
          className="form flex flex-col gap-[18px] items-start w-full max-w-[560px]"
          onSubmit={handleSubmit(onSubmit)}
        >
          <Input
            placeholder="Title"
            {...register('title')}
            error={errors.title?.message}
          />
          <Input
            type="number"
            placeholder="Publishing year"
            {...register('publishingYear', { valueAsNumber: true })}
            error={errors.publishingYear?.message}
          />
          {error && (
            <div className="w-full max-w-[320px] p-4 bg-red-500/10 border border-red-500/30 rounded-lg">
              <div className="space-y-2">
                {(typeof error === 'string' ? error : String(error))
                  .split('\n')
                  .filter((msg) => msg.trim())
                  .map((msg, index) => (
                    <p key={index} className="text-red-400 text-sm font-medium">
                      • {msg.trim()}
                    </p>
                  ))}
              </div>
            </div>
          )}
          <div className="form-actions flex gap-[18px] mt-[22px]">
            <Button variant="ghost" type="button" onClick={handleCancel}>
              Cancel
            </Button>
            <Button type="submit" disabled={isLoading}>
              {isLoading ? 'Updating...' : 'Update'}
            </Button>
          </div>
        </form>
      </div>
      <Wave variant="large" />
    </section>
  );
}

